<template>
  <el-container>
    <el-main v-loading="loading">
      <el-table :data="chagencyinList">
        <el-table-column prop="id" label="中医机构ID" />
        <el-table-column prop="name" label="中医机构名称" />
        <el-table-column prop="contactPerson" label="联系人" />
        <el-table-column prop="phone" label="联系电话" />
        <el-table-column prop="count" label="分院数量" />
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button @click="branchClick(scope.row)" type="text" size="small">分院管理</el-button>
            <el-button type="text" size="small" @click="delMsg(scope.row)">下线</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>确认下线该条信息？</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
// import { phyexaminInfo } from "../../../../data/hosNetwork";
export default {
  name: "medicineTable",
  props:['data', 'loading'],
  data() {
    return {
      chagencyinList: [],
      // chagencyinList: Array(8).fill(phyexaminInfo),
      dialogVisible: false,
      id:''
    };
  },
  methods: {
    handleClick(params) {
      this.id = params.id;
      this.$router.push(`addOrg?type=edit&code=1&id=${this.id}`);
    },
    branchClick(params){
      this.id = params.id;
      this.$router.push(`branch?code=1&id=${this.id}`);
    },    
    delMsg(params) {
      this.id = params.id;
      this.dialogVisible = true;
    },
    sure(){
      this.api.delete(`/chinaMed/${this.id}`).then(()=>{
        this.chagencyinList = this.chagencyinList.filter(item=>{
          return item.id !== this.id
        })
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
      this.dialogVisible = false
    }   
  },
  watch:{
    data(newValue){
      this.chagencyinList = newValue;
    }
  }
};
</script>
<style lang="less" scoped>
</style>